<template>
  <div class="list">
    <article
      v-for="pokemon in pokemonsFiltered"
      :key="pokemon.order"
      @click="showPokemonDetail(pokemon)"
    >
      <img :src="picture + '/' + pokemon.name + '.png'" />
      <h1>{{ pokemon.name }}</h1>
    </article>
  </div>
</template>

<script>
import axios from "axios";
import config from "../config/config";
import lodash from "lodash";
export default {
  name: "PokemonList",
  props: ["pokemonSearch"],
  data() {
    return {
      pokemons: [],
      picture: config.IMG_URL,
    };
  },
  computed: {
    pokemonsFiltered() {
      if (this.pokemonSearch == "") {
        return this.pokemons;
      } else {
        // return lodash.filter(this.pokemons, ["name", this.pokemonSearch]);
        const regex = new RegExp(this.pokemonSearch + ".*");
        return lodash.filter(this.pokemons, function (o) {
          return regex.test(o.name);
        });
      }
    },
  },
  beforeMount() {
    axios
      .get(config.API_URL + "/pokemon?offset=0&limit=721")
      .then((response) => {
        this.pokemons = response.data.results;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    showPokemonDetail(pokemon) {
      this.$emit("showPokemonDetail", pokemon);
    },
  },
};
</script>

<style lang="scss" scoped>
.list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  grid-gap: 10px;
  width: 100%;
  max-width: 510px;
}
article {
  height: 180px;
  background-color: #efefef;
  text-align: center;
  text-transform: capitalize;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2), 0 10px 10px rgba(0, 0, 0, 0.2);
}
h3 {
  margin: 0;
}
#scroll-trigger {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 150px;
  font-size: 2rem;
  color: #efefef;
}

img {
  width: 96px;
  height: 96px;
}
</style>
