<template>
  <div class="container">
    <PokemonSearch
    @searchPokemonEmit="setPokemonSearch" 
    />
    <PokemonDetail
      @closePokemonDetail="closeDetail"
      :pokemonUrl="pokemonUrl"
      v-if="open === true"
    />
    <PokemonList
      @showPokemonDetail="openDetail"
      :pokemonSearch="pokemonSearch"
    />
  </div>
</template>

<script>
import PokemonDetail from "../components/PokemonDetail.vue";
import PokemonList from "../components/PokemonList.vue";
import PokemonSearch from "../components/PokemonSearch.vue";
import config from "../config/config";

export default {
  name: "Pokedex",
  data() {
    return {
      url: config.API_URL,
      open: false,
      pokemonUrl: "",
      pokemonSearch: "",
    };
  },
  methods: {
    openDetail(pokemon) {
      this.open = true;
      this.pokemonUrl = pokemon.url;
    },
    closeDetail() {
      this.open = false;
    },
    setPokemonSearch(question) {
      this.pokemonSearch = question;
    },
  },
  components: {
    PokemonDetail,
    PokemonList,
    PokemonSearch,
  },
};
</script>

<style lang="scss" scoped>
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 10px;
  width: calc(100% - 20px);
  min-height: calc(100vh - 20px);
  //background: radial-gradient(#ffbf0b, #e20000);

  font-family: "Acme", arial;
  font-size: 1rem;
  font-weight: normal;
}

h1 {
  color: #efefef;
}
</style>
